/* Estilos para el componente Hero */
.hero {
    position: relative;
    overflow: hidden;
    height: 100vb;
    /* Ajusta la altura según tus necesidades */
}

.hero-slider {
    display: flex;
    overflow: hidden;
    height: 100%;
}

.slider-item {
    flex: 0 0 100%;
    /* Hace que los elementos ocupen todo el ancho del contenedor */
    position: relative;
}

.slider-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* Ajusta el tamaño y la posición de la imagen según el contenedor */
}

.slider-text {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    padding: 20px;
    background-color: rgba(0, 0, 0, 0.6);
    /* Fondo semi-transparente */
    color: #fff;
    width: 50%;
    /* Ancho del texto (puedes ajustarlo) */
}

.slider-text h1 {
    font-size: 24px;
    margin: 0;
}

/* Estilos para hacerlo responsive */
@media (max-width: 768px) {
    .hero {
        height: 300px;
        /* Ajusta la altura para pantallas más pequeñas */
    }

    .slider-text {
        width: 80%;
        /* Ajusta el ancho del texto para pantallas más pequeñas */
    }

    .slider-text h1 {
        font-size: 18px;
        /* Ajusta el tamaño de fuente para pantallas más pequeñas */
    }
}

