/* Loader */
.loader {
    margin: auto;
    margin-top: 10%;
    width: 200px;
    height: 100px;
}

@-webkit-keyframes loader {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 0;
    }
}

@keyframes loader {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 0;
    }
}

.loader::after,
.loader::before {
    -webkit-animation: loader 2s linear infinite;
            animation: loader 2s linear infinite;
    content: '';
    border: 2px solid #26A86F;
    width: 100px;
    height: 100px;
    position: absolute;
    border-radius: 50%;
}

.loader::after {
    opacity: 0;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}

